.modal {
  position: fixed;
  top: 0;
  right: -100%;
  width: 340px;
  height: 100%;
  /* height: 100vh; */
  background-color: var(--bg-light);
  color: var(--text);
  z-index: 999;
  transition: right 0.3s ease-in-out;
  padding: 120px 40px;
  display: flex;
  flex-direction: column;
}

.show {
  right: 0;
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  background: none;
  border: none;
  color: var(--title);
  cursor: pointer;
}

.menuList {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 60px;
}

.menuItem {
  color: var(--title);
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 10%;
  line-height: 100%;
  text-decoration: none;
}

.menuItem:hover {
  color: var(--highlight);
}

.contactContainer {
  display: flex;
  flex-direction: column;
  gap: 24px;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 60px;
}

.button {
  padding: 12px 20px;
  max-width: 160px;
  background-color: transparent;
  border: 1px solid var(--highlight);
  border-radius: 25px;
}

.btnLink {
  color: var(--title);
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 9%;
}

.socialContainer {
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin-bottom: 20px;
}

.line {
  margin-top: 7px;
  width: 60px;
  height: 1px;
  background-color: var(--title);
}

.socialLinks {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.subtext {
  color: var(--title);
  font-family: 'Open Sans', sans-serif;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0;
}

.subtext:hover {
  color: var(--highlight);
}
